import React,{useState} from 'react'
import "./Login.css"
import {auth} from "./firebase";
import {login} from "./features/userSlice";
import {useDispatch} from "react-redux";
function Login() {

    const [name,setName]=useState("");
    const [email,setEmail]=useState("");
    const [password,setPassword]=useState("");
    const [profilePic,setProfilePic]=useState("");
    const dispatch=useDispatch();
   
    const loginToApp=(e)=>{
        e.preventDefault();
        auth.signInWithEmailAndPassword(email,password)
        .then(userAuth=>{
           dispatch(login({
               email:userAuth.user.email,
               uid:userAuth.user.uid,
               displayName:userAuth.user.displayName,
               photoUrl:userAuth.user.photoURL
           })) 
        }).catch(error=>alert(error.message))
    };
    
    const register=()=>{
        if(!name && !email)
        {
          return alert("Please enter credentials");  
        }
        auth.createUserWithEmailAndPassword(email,password)
        .then((userAuth)=>{
            //Firebase
            //userAuth.user returned from firebase
            userAuth.user.updateProfile({
                displayName:name,
                photoURL:profilePic
            })
            .then(()=>{
                dispatch(login({
                    email:userAuth.user.email,
                    uid:userAuth.user.uid,
                    displayName:name,
                    photoUrl:profilePic,
                }))
            })
        }).catch(error=>alert(error.message))
    };


    return (
        <div className="login">
            <img
            src="data:image/png;base64,"
            alt="No Image"
            />
            <form>
                <input value={name} onChange={(e)=>setName(e.target.value)} placeholder="FullName" type="text"/>
                <input value={profilePic} onChange={(e)=>setProfilePic(e.target.value)} placeholder="Profile Pic" type="text"/>
                <input value={email} onChange={(e)=>setEmail(e.target.value)} placeholder="Email" type="email"/>
                <input value={password} onChange={(e)=>setPassword(e.target.value)} placeholder="Password" type="password"/>
                <button type="submit" onClick={loginToApp}>Sign In</button>
            </form>
            <p>Not a member?
                <span className="login__register" 
                onClick={register}> Register Now</span>
            </p>
        </div>
    )
}

export default Login
